import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserManagementComponent } from './pages/user-management/user-management.component';
import { MyProfileComponent } from './pages/my-profile/my-profile.component';
import {DashboardComponent} from './pages/dashboard/dashboard.component'
import { StaticContentManagementComponent } from './pages/static-content-management/static-content-management.component';
import { StaticTermsViewComponent } from './pages/static-terms-view/static-terms-view.component';
import { StaticTermsEditComponent } from './pages/static-terms-edit/static-terms-edit.component';
import { ChatManagementComponent } from './pages/chat-management/chat-management.component';
import { ViewUserComponent } from './pages/view-user/view-user.component';
import { PackageManagementComponent } from './pages/package-management/package-management.component';
import { FaqManagementComponent } from './pages/faq-management/faq-management.component';
import { FaqEditComponent } from './pages/faq-edit/faq-edit.component';
import { FaqViewComponent } from './pages/faq-view/faq-view.component';
import { PackageEditComponent } from './pages/package-edit/package-edit.component';
import { StaticHomeManagementComponent } from './pages/static-home-management/static-home-management.component';

const routes: Routes = [
  { path: '', redirectTo: 'landing', pathMatch: 'full' },
 
 { path: 'landing',
  loadChildren: () => import('./landing/landing.module').then( m => m.LandingModule)},
  { path: 'dashboard', component: DashboardComponent },
  { path: 'users', component: UserManagementComponent },
  { path: "static-content-management/:id", component: StaticContentManagementComponent },
  { path: 'my-profile', component: MyProfileComponent },
  { path: 'chat-management', component: ChatManagementComponent },
  { path: 'view-user/:id', component: ViewUserComponent },
  { path: 'terms-view/:id', component: StaticTermsViewComponent },
  { path: 'terms-edit/:id', component: StaticTermsEditComponent },
  { path: 'packages-management', component: PackageManagementComponent },
  {path:'faq-management',component:FaqManagementComponent},
  {path:'faq-view/:id',component:FaqViewComponent},
  {path:'faq-edit/:id',component:FaqEditComponent},
  {path:'package-edit/:id',component:PackageEditComponent},
  {path:'home-management/:id',component:StaticHomeManagementComponent}
]

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
