import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MainServicesService } from 'src/app/main-services.service';

@Component({
  selector: 'app-faq-view',
  templateUrl: './faq-view.component.html',
  styleUrls: ['./faq-view.component.css']
})
export class FaqViewComponent implements OnInit {

  static: any ={}
  title:any=''
  // activatedRoute: any;
  id: any;


  constructor(public router: Router, public service: MainServicesService, public activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    
    this.getId()
  }
  //...............get Static Id from Params.................//
  getId() {
    this.activatedRoute.params.subscribe(params => {

      this.id = params.id

      this.staticData()
    })
  }
  //..........................Get Static Data..........................//
  staticData() {
    this.service.getApi(`static/viewFaq?faqId=${this.id}`, 1).subscribe(res => {
      if (res['responseCode'] == 200) {
        this.static = res['result'].FAQ[0]
        // this.title=this.static.result[0].title
      }
      else {
        this.service.toastErr(res['responseMessage'])
      }

    }, error => {
      this.service.toastErr('Something Went Wrong')

    })

  }

  goBack() {
    history.back();
    // window.location.origin;
  }




}
