import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MainServicesService } from 'src/app/main-services.service';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-package-management',
  templateUrl: './package-management.component.html',
  styleUrls: ['./package-management.component.css']
})
export class PackageManagementComponent implements OnInit {

  packages: any = [];
  tabModeSelect: any = "FAQ";
  id: any;

  constructor(public router: Router, public service: MainServicesService, public activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.staticData()
  }


  //..........................Get Static Data..........................//
  staticData() {
    this.service.getApi(`static/allPackages`, 1).subscribe(res => {
      console.log("allPackages====>",res["result"])
      if (res['responseCode'] == 200) {
        this.packages = res["result"]
      }
      else {
        this.service.toastErr(res['responseMessage'])
      }
    }, error => {
      this.service.toastErr('Something Went Wrong')

    })

  }

  

}
